@charset "utf-8";
@import url('https://webfontworld.github.io/ridi/RidiBatang.css');

:root{
  --main_font: "RidiBatang", sans-serif;
  --font_main: #333333;
  --bg_main: #ffffff;
  --main_gra: linear-gradient(to bottom, #FBE6EB 0%, #D0F1F9 100%);

  --bk_shadow: 5px 5px 10px rgba(255,255,255,0.25), inset 5px 5px 10px #000;
  --wh_shadow: 5px 5px 10px rgba(0,0,0,0.1), inset 5px 5px 10px rgba(255,255,255,0.5);

  --f_s_footer: 0.88rem;
  --f_s_main: 1rem;
  --f_s_point: 1.15rem;
  --f_s_subtxt: 1.25rem;
  --f_s_subtitle: 1.5rem;
  --f_s_maintitle: 2rem;
}

html{
  width: min(100vw, 100%); height: min(100vh, max-content);
  background: var(--main_gra);
  font-family: var(--main_font);
  font-size: 16px;
  color: var(--font_main);
  line-height: 160%;
}
@media screen and (max-width: 768px) {
  html{
    font-size: 14px;
  }
}

body{overflow-X: hidden;}
main > div:not(div:first-child){padding: 1rem;}
*{
  box-sizing: border-box; padding: 0; margin: 0;
}
a{color: inherit; text-decoration: none; display: block;}
ul, li{list-style: none;}