@charset "utf-8";

header{
  text-align: center;
  font-size: var(--f_s_subtxt);
  font-weight: bold;
}

/* 처음 헤더 */
.header_top{
  /* display: none; */
  position: fixed; top: 0; left: 0; z-index: 1000;
  width: 100%; height: 60px;
  align-content: center;
  /* padding-top: 1rem; */
  overflow: hidden;
  transition: 1.5s;
  background: transparent;
}
.header_act{
  background: rgba(255,255,255,0.85);
  backdrop-filter: blur(3px);
}
/* 스크롤 내렸을 때 헤더 */
.header_bottom{
  display: none;
  position: fixed; top: 0; left: 0; z-index: 1000;
  width: 100vw; max-height: max-content;
  border-bottom: 1px solid var(--font_main);
}
.header_bottom ul{
  width: min(1100px, 100%); height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: var(--f_s_subtxt);
  padding-top: 1rem;
}
.header_bottom li{
  width: 15%; height: 100%;
  align-content: center;
  padding-top: 0.7rem;
}
/* 내비게이션 액티브 서식 */
.nav_act{
  background: var(--font_main);
  color: var(--bg_main);
  border-radius: var(--f_s_maintitle) var(--f_s_maintitle) 0 0;
  box-shadow: var(--bk_shadow);
}

/* 모바일용 헤더 토글 */
.nav_toggle{
  display: none;
  position: fixed; top: 0; left: 50%; z-index: 1001;
  transform: translateX(-50%);
  width: 40px; height: 40px;
  /* border: 1px solid var(--font_main); */
  overflow: hidden;
  margin-block: 10px;
}
.nav_toggle > .bar{
  position: absolute; left: 1px;
  display: inline-block;
  width: 38px; height: 3px;
  background: var(--font_main);
  transition: 0.3s;
}
.nav_toggle > .bar1{top: 5px;}
.nav_toggle > .bar2{top: 18px;}
.nav_toggle > .bar3{bottom: 5px;}
.bar1_toggle{transform: rotate(45deg) translate(10px, 10px);}
.bar2_toggle{transform: translateX(-40px);}
.bar3_toggle{transform: rotate(-45deg) translate(10px, -10px);}

