@charset "utf-8";

main > div:not(div:first-child){
  width: min(1100px, 100%);
  margin: 0 auto;
}

/* 첫번째 메인 - 비주얼 */
.main1{
  position: relative;
  width: 100%; height: 105vh;
  /* padding-top: 60px; */
  text-align: center;
  align-content: center;
}
/* 보조텍스트 */
.main1 > p{
  position: relative; z-index: 1;
  font-size: var(--f_s_subtxt);
  transform: translateY(-130px);
}
/* 포트폴리오 제목 */
.main1 > h1{
  position: relative; z-index: 1;
  font-size: 4rem;
  letter-spacing: 0.35rem;
  margin-right: -0.7rem;
  transform: translateY(-100px);
}
/* 이미지 */
.main1 > img:nth-of-type(1){
  width: 400px;
  object-fit: cover;
  position: absolute;
  top: 10%; left: 50%;
  transform: translateX(-50%);
  animation: moon 0.8s infinite;
}
.main1 > img:not(img:nth-of-type(1)){
  width: 100%; min-height: 1100px;
  object-fit: cover;
  position: absolute;
  left: 50%; bottom: 0%;
  transform: translateX(-50%);
}
.main1 > img:nth-of-type(2){animation: clouds 2s infinite;}
.main1 > img:nth-of-type(3){animation: clouds 4s infinite;}
.main1 > img:nth-of-type(4){animation: clouds 2.5s infinite;}
@keyframes clouds {
  0% {
    transform: translate(-50%, 0%);
    animation-timing-function: ease-in;
  }
  50% {
    transform: translate(-50%, 3%);
    animation-timing-function: ease-out;  
  }
  100% {
    transform: translate(-50%, 0%);
  }
}
@keyframes moon {
  0% {
    opacity: 1;
    animation-timing-function: ease-in;
  }
  50% {
    opacity: 0.7;
    animation-timing-function: ease-out;
  }
  100% {
    opacity: 1;
  }
}


/* 메인 제목 공통 */
main > div > h2{
  position: relative; z-index: 1;
  font-weight: normal;
  text-align: center;
  margin-block: 10rem 3rem;
}
main > div > h2 > span{font-weight: bold;}

main > div.main2 > h2{
  margin-block: 0rem 3rem;
}
/* 두번째 메인 - 프로필 */
.skill_main{
  display: flex; gap: 1rem;
  flex-wrap: wrap;
  width: 100%; height: 100%;
  justify-content: space-between;
  text-align: center;
  margin-bottom: 3rem;
}
.skill_main > li{
  /* flex: 1; */
  min-width: 30%; min-height: 220px;
  margin-bottom: 2rem;
  cursor: pointer;
}
.skill_main > li > p{
  font-weight: bold;
  font-size: var(--f_s_subtxt);
  margin-top: 1rem;
}
.skill_main ul{
  height: 100%;
  align-content: center;
  background: rgba(255,255,255,0.2);
  border-radius: 2rem;
  box-shadow: var(--wh_shadow);
  border: 1px solid var(--bg_main);
  transition: 0.4s;
}
.skill_main ul.skill_act{
  color: var(--bg_main);
  background: var(--font_main);
  box-shadow: var(--bk_shadow);
  border: 1px solid var(--font_main);
}
/* 스킬 설명 박스 */
.skill_text{
  width: 100%;
  color: var(--bg_main);
}
.skill_text > ul{
  background: var(--font_main);
  box-shadow: var(--bk_shadow);
  border: 1px solid var(--font_main);
  border-radius: 2rem;
  padding: 1rem 2rem;
  padding-bottom: 1.5rem;
  transition: opacity 0.5s ease-in-out;
  opacity: 0;
}
.skill_text > ul.fadeIn {opacity: 1;}
.skill_text > ul.fadeOut {opacity: 0;}
.skill_text > ul > li{
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.skill_text > ul > li > p:nth-child(1){
  font-size: var(--f_s_maintitle);
  padding-right: 1rem;
}
.skill_text > ul > li > p:nth-child(2){
  width: 110px;
}
.skill_text > ul > li > p:nth-child(3){
  width: 100%;
}
.skill_text .fadeIn {
  opacity: 1;
  animation: fadeIn ease-in-out 0.5s forwards;
}
@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

/* 세번째 메인 - 자격증 */
.Certi > li, .skills{
  position: relative;
  width: 48%; height: 80px;
  border-radius: 1rem;
  font-size: var(--f_s_subtxt);
  align-content: center;
  padding-inline: 1rem;
  clear: both;
  padding-top: 0.4rem;
}
.Certi > li{
  background: rgba(255,255,255,0.2);
  box-shadow: var(--wh_shadow);
  border: 1px solid var(--bg_main);
  font-weight: bold;
}
.Certi > li > span{
  float: right;
  display: block;
  font-size: var(--f_s_main);
  font-weight: normal;
}
.Certi .skills{
  position: absolute; top: 0; left: 0; z-index: 1;
  width: 100%; height: 100%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  font-size: var(--f_s_footer);
  color: var(--bg_main);
  background: var(--font_main);
  box-shadow: var(--bk_shadow);
  border: 1px solid var(--font_main);
  font-weight: normal;
}
.Certi .fadeIn {opacity: 1;}
.Certi .fadeOut {opacity: 0;}
.Certi > li:nth-of-type(even), .Certi .skills:nth-of-type(even){
  float: right;
  clear: both;
}
.Certi > li:nth-of-type(2), .Certi .skills:nth-of-type(2){transform: translateY(-30%);}
.Certi > li:nth-of-type(3), .Certi .skills:nth-of-type(3){transform: translateY(-60%);}
.Certi > li:nth-of-type(4), .Certi .skills:nth-of-type(4){transform: translateY(-90%);}
.Certi > li:nth-of-type(5), .Certi .skills:nth-of-type(5){transform: translateY(-120%);}
.Certi > li:nth-of-type(6), .Certi .skills:nth-of-type(6){transform: translateY(-150%);}
.Certi > li:nth-of-type(7), .Certi .skills:nth-of-type(7){transform: translateY(-200%);}
@media screen and (max-width: 768px) {
  .Certi > li, .skills{
    width: 100%; height: 70px;
    transform: translateY(0) !important;
    margin-bottom: 1rem;
  }
}

/* 네번째 메인 - 타임라인 */
main > div.main4 > h2{
  margin-top: 0rem;
}
@media screen and (max-width: 768px) {
  main > div.main4 > h2{
    margin-block: 10rem 5rem;
  }
}
.timeline_box{position: relative;}
.timeline_box::before{
  content: "";
  position: absolute;
  left: 9.8rem; top: -2%;
  width: 2px; height: 104%;
  background: rgba(0,0,0,0.15);
}
.timeline{
  display: flex;
  margin-bottom: 4rem;
}
.timeline > p{
  position: relative;
  width: 10.5rem; margin-right: 1rem;
}
.timeline > p::before{
  content: "";
  width: 1rem; height: 1rem;
  background: var(--font_main);
  border-radius: 50%;
  position: absolute;
  top: 0.15rem; right: 0.15rem;
}
.timeline > div{width: calc(100% - 11.5rem);}
.timeline h3{font-size: var(--f_s_point); margin-bottom: 0.5rem;}
.timeline dl{
  width: calc(100% - 1rem);
  display: flex;
  flex-wrap: wrap;
  font-size: var(--f_s_footer);
}
.timeline dt{width: 5.5rem; font-weight: bold; margin-right: 1rem;}
.timeline dd{width: calc(100% - 6.5rem);}

/* 다섯번째 박스 - 프로젝트 */
.project_box{
  display: flex; gap: 1rem;
  width: 100%;
  margin-bottom: 5rem;
}
.project_box:nth-of-type(even){
  flex-direction: row-reverse;
}
.project_box img{
  width: 100%;
}
.main_box, .sub_box{width: calc(50% - 0.5rem);}
@media screen and (max-width: 768px) {
  .project_box{flex-wrap: wrap; width: 100%;}
  .main_box, .sub_box{width: 100%;}
}
/* 메인박스 */
.main_box{
  background: var(--font_main);
  border-radius: var(--f_s_maintitle);
  box-shadow: var(--bk_shadow);
  padding: 1rem;
  color: var(--bg_main);
}
.main_box h3{
  line-height: 250%;
  text-align: center;
  font-size: var(--f_s_point);
}
.main_box p{
  margin-bottom: 1rem;
}
.main_box dl{
  width: calc(100% - 1rem);
  display: flex;
  flex-wrap: wrap;
  font-size: var(--f_s_footer);
}
.main_box dt{width: 4rem; font-weight: bold; margin-right: 1rem;}
.main_box dd{width: calc(100% - 5rem);}
.main_box ul{
  display: flex; gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}
.main_box li{
  width: 30%; line-height: 300%;
  text-align: center;
  padding-top: 0.2rem;
  border-radius: var(--f_s_main);
  box-shadow: 2px 2px 4px rgba(0,0,0,0.5), inset 2px 2px 4px rgba(255,255,255,0.2);
  transition: 0.4s;
}
.main_box li:hover{
  box-shadow: 2px 2px 4px rgba(255,255,255,0.25), inset 2px 2px 4px #000;
}
/* 서브박스 */
.sub_box{
  display: flex; gap: 1rem;
  flex-wrap: wrap;
}
.sub_box p{
  line-height: 290%;
  text-align: center;
}
.sub_box li{
  width: calc(50% - 0.5rem);
  box-shadow: var(--wh_shadow);
  padding: 1rem;
  border-radius: var(--f_s_maintitle);
}

/* 여섯번째 박스 - 콘택트 */
.main6 ul{
  display: flex; gap: 1rem;
  justify-content: center;
  margin-block: 4rem 15rem;
}
.main6 li{
  text-align: center;
  width: 18%;
  box-shadow: var(--wh_shadow);
  border-radius: var(--f_s_main);
  transition: 0.4s;
}
.main6 li:hover{
  box-shadow: var(--bk_shadow);
  color: var(--bg_main);
  background: var(--font_main);
}
.main6 li::after{
  display: block;
  content: "";
  padding-bottom: 20%;
}
.main6 li p{font-size: var(--f_s_footer);}
.main6 li p:nth-child(1){
  margin-block: 20% 1rem;
  font-size: var(--f_s_maintitle);
}
.main6 li p:nth-child(2){font-weight: bold;}

@media screen and (max-width: 768px) {
  .main6 ul{flex-wrap: wrap;}
  .main6 li{width: 30%;}
}
@media screen and (max-width: 500px) {
  .main6 li{width: calc(50% - 0.5rem);}
}